<template>
    <view class="things_publish_car">
        <!-- tabbar -->
        <cu-custom><block slot="content">货车详情</block></cu-custom>
        <view class="car_info">
            <view class="things_width_full form_info">
                <view class="things_width_full flex justify-start align-center user_info">
                    <view class="imageBox">
                        <image class="user_cover" :src="car_form_data.path" mode="widthFix"/>
                    </view>
                    <view class="name">{{car_form_data.nickname}}</view>
                    <view class="flex justify-start align-center score">
                        <image class="score_icon" src="./../../../../../static/images/global/score_icon.png" mode="widthFix" />
                        <view class="score_num">{{car_form_data.reputation}}</view>
                        <view class="identity_text">{{car_form_data.userType}}</view>
                    </view>
                </view>
                <view class="cu-form-group">
                    <view class="title">运输价格：</view>
                    <view class="inputText">￥{{car_form_data.totalMoney}}</view>
                </view>
                <view class="cu-form-group">
                    <view class="title">车辆类型：</view>
                    <view class="inputText">{{car_form_data.CarsType}}</view>
                </view>
                <view class="cu-form-group">
                    <view class="title">车牌号：</view>
                    <view class="inputText">{{car_form_data.carNumber}}</view>
                </view>
                <view class="cu-form-group">
                    <view class="title">挂车牌号：</view>
                    <view class="inputText">{{car_form_data.listingCarNumber}}</view>
                </view>
                <view class="cu-form-group">
                    <view class="title">车内灌质：</view>
                    <view class="inputText">{{car_form_data.carFilling}}</view>
                </view>
                <view class="cu-form-group">
                    <view class="title">灌体立方数：</view>
                    <view class="inputText">{{car_form_data.cubicNumber}}</view>
                </view>
                <view class="cu-form-group">
                    <view class="title">车辆所属公司：</view>
                    <view class="inputText">{{car_form_data.carCompany}}</view>
                </view>
                <view class="cu-form-group">
                    <view class="title">开始时间：</view>
                    <view class="inputText">{{car_form_data.startTime}}</view>
                </view>
                <view class="cu-form-group">
                    <view class="title">结束时间：</view>
                    <view class="inputText">{{car_form_data.endTime}}</view>
                </view>
                <view class="cu-form-group">
                    <view class="title">备注：</view>
                    <view class="inputText">{{car_form_data.remark}}</view>
                </view>
            </view>
            <view class="flex justify-center align-center btn_box">
                <!-- 开始抢单 -->
                <button class="cu-btn publish_btn" :data-id="order_id" @tap="linkToCarFill">开始抢单</button>
            </view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
    </view>
</template>
<script>
import { grabGetCarOrderDetail_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            order_id: '',
            // 全局按钮状态
            buttonType: false,
            car_form_data: null
        }
    },
    watch: {
        '$store.getters.base_appBaseData': function(val) {
            that.appBaseData = val;
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    methods: {
         ...mapMutations([
            'modal',
            'base'
        ]),
        /**跳转到抢车单钱填表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToCarFill(e) {
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/grabOrderHall/car/fill/index?order_id=${e.currentTarget.dataset.id}`,
                complete: ()=>{
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**获取货物订单信息
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getCarInfo(e) {
            let params = {
                order_id: that.order_id
            };
            grabGetCarOrderDetail_api(params,that).then(res=>{
                that.car_form_data = res;
            }).catch(err=>{
                console.log(err)
            })
        }
},
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.order_id || options.order_id == ''|| options.order_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该订单不存在',linkUrl:1}});
            return false;
        }
        that.order_id = options.order_id;
        that.getCarInfo();
    }
}
</script>

<style lang="scss" scoped>
    @import './index';
</style>